@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet {
  @media (min-width: 640px) {
    @content;
  }
};

@mixin onDesktop-s {
  @media (min-width: 1024px) {
    @content;
  }
 }

 @mixin onDesktop-m {
  @media (min-width: 1600px) {
    @content;
  }
 }

 @mixin onDesktop-l {
  @media (min-width: 2560px) {
    @content;
  }
 }
